@import url('https://fonts.googleapis.com/css2?family=Montserrat+Alternates:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');


.carabao{
    position: relative;
    width: 100%;
    height: 700px;
    font-weight: 500;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  
.carabao::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-image: url('./../images/manholdingcarabao.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    filter: brightness(50%);
    z-index: -1;
}
.carabao__container{
    width: 100%;
}
.carabao__content{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}
.carabao__content-title{
    font-size: 80px;
    font-weight: 600;
    line-height: 88px;
    letter-spacing: 0.02em;
    text-align: center;
    color: #fff;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
}
.carabao__content-text{
    font-family: "Montserrat Alternates", sans-serif;
    font-size: 20px;
    color: #fff;
}
.carabao-names-identity{
    display: flex;
    align-items: center;
    gap: 15px;
    margin-left: 30px;
    margin-bottom: 30px;
}
.carabao-names-identity a{
    font-size: 16px;
    font-weight: 500;
    line-height: 28px;
    color: #fff;
    letter-spacing: 0.02em;
    text-align: center;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;
}





/* CARABAO ABOUT */
.carabao_about{
    width: 100%;
    height: 600px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 50px 0;
}
.carabao_about_container{
    width: 1182px;
    display: flex;
    align-items: center;
    gap: 100px;
}
.carabao_about_container_text{
    max-width: 526px;
}
.carabao_about_container_text p{
    font-family: "Montserrat Alternates", sans-serif;
    font-size: 18px;
    color: #000;
    line-height: 28px;
}


.carabao_influencers{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 20px;
    padding: 50px 0;
}
.carabao_influencer_container{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 70px;
}
.carabao_influencer_container_text{
    max-width: 955px;
}
.carabao_influencer_container_text p{
    font-family: "Montserrat Alternates", sans-serif;
    font-size: 18px;
    color: #000;
    line-height: 28px;
    text-align: center;
}

/* BRAND SPONSORS */
.brand__sponsor{
    width: 100%;
    height: 650px;
    display: flex;
    align-items: center;
    justify-content: center;
}
.brand__sponsor_container{
    max-width: 1106px;
    height: 250px;
    display: flex;
    flex-wrap: wrap;
    gap: 80px;
}